*{margin:0;padding:0;box-sizing: border-box;}
.section-game .list{
  display: flex;
  font-size: 0;
  flex-wrap:wrap;
}
.section-game .list-item{
    width: 25%;
    padding: 1%;
}
.section-game .list-item .button_open{
  overflow: hidden;
  width: 100%;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 10px;
}
.section-game .list-item img{
  max-width: 100%;
}